import React from 'react';

import { 
    Box,
    Heading,
    Image,
 } from 'grommet';

class ProductUsed extends React.Component {
    render() {
        const height = this.props.ukuran !== 'small' ? '40rem' : '15rem'
        const padding = this.props.ukuran !== 'small' ? { "vertical": "5rem", "horizontal": "10rem" } : { "vertical": "3rem", "horizontal": "1rem" }

        return(
            <Box pad={padding} align='center' background='light-2'>
                <Box pad='small'>
                    <Heading level='1' >Produk yang kami gunakan</Heading>
                </Box>
                <Box
                    round='medium'
                    border={{ "color": "light" }}
                    elevation='large'
                    overflow='scroll'
                    align='center'
                    height={height} >
                    <Image fit='contain' src='https://i.imgsafe.org/25/25fcbc3ebe.jpeg' />
                </Box>
            </Box>
        );
    }
}

export default ProductUsed;