import React from 'react';

import { 
    Box,
    Carousel,
    Image,
} from 'grommet';

class Hero extends React.Component {

    render() {
        const height = this.props.ukuran !== 'small' ? '40rem' : '20rem';
        
        return(
            <Box height={height} justify='center' overflow='hidden' elevation='medium' direction='row' >
                <Carousel fill>
                    <Box height={height} >
                        <Image fit='cover' src='https://i.imgsafe.org/26/2601c77963.jpeg' />
                    </Box>
                    <Box height={height} >
                        <Image fit='cover' src='https://i.imgsafe.org/26/2602095b67.jpeg' />
                    </Box>
                    <Box height={height} >
                        <Image fit='cover' src='https://i.imgsafe.org/26/2603742198.jpeg' />
                    </Box>
                </Carousel>
            </Box>
        );
    }
}

export default Hero;