import React from 'react';
import { Grommet, ResponsiveContext, Box } from 'grommet';

import Appbar from './modules/components/Appbar';
import Hero from './modules/views/Hero';
import Description from './modules/views/Description';
import ProductUsed from './modules/views/ProductUsed';
import Footer from './modules/components/Footer';

class App extends React.Component {
  render() {

    const theme = {
      global: {
        font: {
          family: 'Roboto',
          size: '14px',
          height: '20px'
        }
      }
    }

    return (
      <Grommet theme={theme}>
        <ResponsiveContext.Consumer>
          {(size) => (
            <Box >
              <Appbar />
              <Hero ukuran={size} />
              <Description />
              <ProductUsed ukuran={size} />
              <Footer />
            </Box>
          )}
        </ResponsiveContext.Consumer>
      </Grommet>
    );
  }
}

export default App;